<template>
   <div class="main__info-content-actors">
    <div 
      class="main-info-content-actor" 
      v-for="actor in actors" 
      :key="actor.id"
    >
      <img :src="imageUrl + actor.profile_path" alt="" class="main-info-content-img">
      <span class="main-info-content-name">{{ actor.name }}</span>
    </div>
    </div>
</template>

<script>

import { mapActions, mapState } from 'vuex'

export default {
  data() {
    return {
      actors: null
    }
  },
  props: ['type', 'id','count'],
  methods: {
    ...mapActions('actors', ['getActors'])
  },
  computed: {
    ...mapState(['imageUrl']),
    getCountActors() {
      const type  = this.type == '/movie/' ? 'getMovieActors' : 'getTvActors'
      return this.$store.getters[`actors/${type}`](this.count)
    }
  },
  async mounted() {
    await this.getActors({id: this.id, type: this.type})
    this.actors = this.getCountActors
  },
  unmounted() {
    const reset = this.type == '/movie/' ? 'clearActorsMovie' : 'clearActorsTv'
    this.$store.commit(`actors/${reset}`)
  }
  
}
</script>

<style lang="scss">

.main__info-content-actors {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  max-width: 500px;
  width: 100%;
}
.main-info-content-actor {

  border: 1px solid #FFFFFF;
  border-radius: 25px;
  padding-right: 15px;
  overflow: hidden;
  display: flex;
  align-items: center;
  gap: 10px;
  & img {
    width: 45px;
    height: 50px;
    border-radius: 50%;
  }
  & span {
    font-size: 16px;
    line-height: 30px;
    color: white;
  }
}



</style>